.fixed{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
}
.clean-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.btn-clear {
    border:none;
    background-color: transparent;
    cursor: pointer;
    padding:0;
    outline: none;
}


/*---flex---*/

.flex {
    display: -ms-flexbox;
    display: flex;
}
.inline-flex {
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.justify-center {
    -ms-flex-pack: center;
        justify-content: center;
}

.justify-start {
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.justify-end {
    -ms-flex-pack: end;
        justify-content: flex-end;
}


.align-center {
    -ms-flex-align: center;
        align-items: center;
}

.space-around {
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.space-between {
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.flex-wrap {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.flex-full {
    -ms-flex: 1 1;
        flex: 1 1;
}

.flex-column {
    -ms-flex-direction: column;
        flex-direction: column;
}

.align-start {
    -ms-flex-align: start;
        align-items: flex-start;
}

.align-end {
    -ms-flex-align: end;
        align-items: flex-end;
}





